import './navbar.css';
import { Stack, HStack, VStack } from "@chakra-ui/react";
import { BiShoppingBag } from 'react-icons/bi';
import { FaYoutube } from 'react-icons/fa';
import { FaFacebookSquare } from 'react-icons/fa';
import { FiDivide, FiInstagram } from 'react-icons/fi';
import { FaTwitterSquare } from 'react-icons/fa';
import {useWindowSize} from '@react-hook/window-size'

function Navbar(){
    const [widthOFwindow, heightOFwindow] = useWindowSize({ fps: 60 });
    return(
        <div>
            { widthOFwindow > 600 &&
            <nav class="home-navbar">
                <HStack spacing='1rem'>
                    <img src="IMG_20211008_234925.png" alt='logo' class="home-logo1"/>
                    <a href="#" class='home-navbar-links'>Home</a>
                    <a href="#" class='home-navbar-links'>Shop</a>
                    <a href="#" class='home-navbar-links'>Sign up</a>
                    <BiShoppingBag class='home-shop-icon' />
                </HStack>
                <span class='home-navbar-socal'>
                    <FaYoutube spacing='3rem' class='home-shop-socal-icons'/>
                    <FaFacebookSquare class='home-shop-socal-icons'/>
                    <FiInstagram class='home-shop-socal-icons'/>
                    <FaTwitterSquare class='home-shop-socal-icons'/>
                </span>
            </nav>
            
}
            { widthOFwindow < 600 &&
            <nav class='home-navbar-mobile'>
                <center>
                <img src="IMG_20211008_234925.png" alt='logo' class="home-logo1-mobile"/><br />
                    <FaYoutube class='home-shop-socal-icons-mobile'/>
                    <FaFacebookSquare class='home-shop-socal-icons-mobile'/>
                    <FiInstagram class='home-shop-socal-icons-mobile'/>
                    <FaTwitterSquare class='home-shop-socal-icons-mobile'/>
                </center>
            </nav>
            }
        </div>
    )
}
export default Navbar;