import { Box } from "@chakra-ui/layout";
import VideoBg from "reactjs-videobg";
import './background/background.css';
import Content from "./content/content";
import Navbar from "./navbar/navbar";
import Tabbar from "./tabbar/tabbar";

function Home() {
  return(
    <div>
      <VideoBg videoClass='video' poster={'https://wallpaper.dog/large/11000741.jpg'}><VideoBg.Source src='https://video.wixstatic.com/video/db91b0_01e25e888c8347aca7010bd3af03544c/720p/mp4/file.mp4' type="video/mp4"/></VideoBg>
      <Navbar />
      <Box h='12rem'></Box>
      <Tabbar />
      <Content />
    </div>
  )
}

export default Home;