import { BiLogIn } from 'react-icons/bi';
import { FaYoutube } from 'react-icons/fa';
import { AiFillHome } from 'react-icons/ai';
import { BiShoppingBag } from 'react-icons/bi';
import styled from "styled-components";
import './tabbar.css';
import {useWindowSize} from '@react-hook/window-size'

function Tabbar(){
    const [widthOFwindow, heightOFwindow] = useWindowSize({ fps: 60 });
    return(
        <div>
            { widthOFwindow < 600 &&
            <div style={{width:"100vw" , height:"10vh" , position:"fixed" , left:"0" ,bottom:"0",zIndex:'10'}}  >
            <BottomNavigationV>
            <a href="/home" class="tabbar-navi" ><NavItem className={"tabbar"} ><AiFillHome style={{ fontSize: '7vh' }} /></NavItem></a>
            <a href="/home" class="tabbar-navi" ><NavItem className={"tabbar"} ><BiShoppingBag style={{ fontSize: '7vh' }} /></NavItem></a>
            <a href="/home" class="tabbar-navi" ><NavItem className={"tabbar"} ><BiLogIn style={{ fontSize: '7vh' }} /></NavItem></a>
            <a href="/home" class="tabbar-navi" ><NavItem className={"tabbar"} ><FaYoutube style={{ fontSize: '7vh' }} /></NavItem></a>
            </BottomNavigationV>
            </div>
}
        </div>
    )
}
const BottomNavigationV = styled.div`
position: relative;
width: 100%;
display: flex;
justify-content: space-between;
bottom: 0;
right: 0;
background-color: 0;
z-index: 100;
height: 100%;
border-top: 2px solid #66fcf1;
background: rgba(0, 0, 0, 0.233);
box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.164);
backdrop-filter: blur( 9px );
-webkit-backdrop-filter: blur( 9px );

border: 1px solid rgba( 255, 255, 255, 0.18 );
`;


const NavItem = styled.div`
flex-grow: 1;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
height: 100%;
transition: all 0.3s ease;
width:100%;

`;

const NavText = styled.p`
margin-top: 3px;
font-size: 1vh;
margin-bottom: 5px;
fontWeight:"700"
`;
export default Tabbar;
