import Home from "./home/home";
import AnimatedCursor from "react-animated-cursor";

function App() {
  return(
    <div>
      <AnimatedCursor color='0, 255, 251'   outerAlpha={0.2}  innerSize={15} outerSize={15} innerScale={0.7} outerScale={5}/>
      <Home />
    </div>
  )
}

export default App;